import { useState,  } from "react";
import { DOMAIN } from "@/constants";
import Image from "next/image";

interface ProductSingleImagesProps {
  images: string[];
  mainImage: string;
}

export const ProductSingleImages: React.FC<ProductSingleImagesProps> = ({ images, mainImage }) => {
  const [activeImage, setActiveImage] = useState<string>(mainImage);


  return (
    <div className="flex">
      <div className="flex flex-col">
        {mainImage && (
          <div className="mb-2">
            <Image
              src={`${DOMAIN}/${mainImage}`}
              alt="product"
              width={57}
              height={57}
              onClick={() => setActiveImage(mainImage)}
              className={`w-[56px] h-[56px] cursor-pointer ${
                activeImage === mainImage ? "border border-blue-500" : ""
              }`}
            />
          </div>
        )}
        {images?.map((image: string, index: number) => (
          <div className="mb-2" key={index}>
            <Image
              src={`${DOMAIN}/${image}`}
              alt="product"
              width={57}
              height={57}
              onClick={() => setActiveImage(image)}
              className={`w-[56px] h-[56px] cursor-pointer ${
                activeImage === image ? "border border-blue-500" : ""
              }`}
            />
          </div>
        ))}
      </div>
      <div>
        <Image
          src={`${activeImage ? `${DOMAIN}/${activeImage}` : `${DOMAIN}/${mainImage}`}`}
          alt="product"
          width={400}
          height={400}
          className="w-[400px] h-[400px]"
        />
      </div>
    </div>
  );
};

export default ProductSingleImages;
